<template>
  <div>
    <a-modal v-model="dialog" :dialog-style="{ top: '20px' }" title="ค้นหาห้องประชุม">
      <v-row class="pt-5" align="center" justify="center">
        <v-col>
          <a-alert
           :message="this.message_alert"
            description="โปรดกดเลือกเวลาด้านล่าง เพื่อค้นหาห้องประชุมที่ท่านต้องการ"
          />
        </v-col>
      </v-row>

      <v-row class="" align="center" justify="center">
        <v-col class="" align="center">
          <a-form>
            <a-form-item label="วันที่">
              <a-input
                disabled
                :placeholder="this.select_date_calender_component"
              >
              </a-input>
            </a-form-item>
          </a-form>
        </v-col>
      </v-row>

      <v-row class="" align="center" justify="center">
        <v-col class="" align="center">
          <a-form>
            <a-form-item label="เวลาเริ่มต้น">
              <a-time-picker
                inputReadOnly
                @change="onChange_Start_Time"
                style="width: 100%"
                :value="value_starttime"
                format="HH:mm"
              />
            </a-form-item>
          </a-form>
        </v-col>

        <v-col class="" align="center">
          <a-form>
            <a-form-item label="เวลาสิ้นสุด">
              <div name="disabled-keyboard--input">
                <a-time-picker
                  inputReadOnly
                  @change="onChange_End_Time"
                  style="width: 100%"
                  :value="value_endtime"
                  format="HH:mm"
                />
              </div>
            </a-form-item>
          </a-form>
        </v-col>
      </v-row>

      <template slot="footer">
        <a-button key="back" @click="handleCancel">
          ปิด
        </a-button>
        <a-button key="submit" type="primary" @click="API_search_MeetingRoom()">
          ค้นหา
        </a-button>
      </template>
    </a-modal>

    <DialogAddMeeting ref="DialogAddMeeting"></DialogAddMeeting>
  </div>
</template>
<script>
import DialogAddMeeting from "./DialogAddMeeting.vue";
import moment from "moment";
export default {
  components: {
    DialogAddMeeting,
  },
  data: () => ({
    dialog: false,
    select_to_date: null,
    value_starttime: null,
    value_endtime: null,
    start_time: null,
    end_time: null,
    delayTime: 500,
    select_date_calender_component: null,
    loading: false,
    message_alert:"",
    source_api: {}
  }),
  methods: {
    moment,
    open(data) {
      this.select_date_calender_component = data;
      this.message_alert = "วันที่ท่านเลือก : " + data
      this.dialog = true;
    },
    onChange_Select_Date(date, dateString) {
      if (dateString != null) {
        this.select_to_date = dateString;
      }
    },
    onChange_Start_Time(time, timeString) {
      this.value_starttime = time;
      this.start_time = timeString;
    },
    onChange_End_Time(time, timeString) {
      this.value_endtime = time;
      this.end_time = timeString;
    },

    API_search_MeetingRoom() {
      // console.log(this.select_date_calender_component);
      // console.log(this.start_time);
      // console.log(this.end_time);
      this.loading = true;
      if (this.start_time == null || this.end_time == null) {
        this.$message.error("กรุุณาเลือกข้อมูลให้ครบถ้วน");
      } else {
        this.source_api = {
          meeting_start:
            this.select_date_calender_component + " " + this.start_time,
          meeting_end:
            this.select_date_calender_component + " " + this.end_time,
        };
        console.log(this.source_api);
        console.log(this.$store.state.url_get_API_axios);
        this.axios({
          method: "POST",
          url: "http://192.168.74.254:8000/api/v1/checkAvailableRoom",
          data: this.source_api,
          // headers: { Authorization: "Bearer " + localStorage.token },
        })

          .then((response) => {
            this.loading = false;
            console.log(response.data);
            console.log(response.data[0].Status);
            console.log(response.data[0].value);

            if (response.data[0].Status === "These Rooms are Avaliable") {
              this.loading = false;
              this.data_value = response.data[0].value; 

              this.Open_DialogAdd_event_Meeting()
            } else {
              this.loading = false;
      
            }
          })
          .catch((error) => {
            console.log(error.message);
            // this.loading = false;
            if (error.message == "Request failed with status code 400") {
             this.$message.error('ไม่พบห้องประชุมที่ว่าง');
            }
            //  else if (error.msg === "UNAUTHORIZED") {
            //   this.$message.warning(
            //     "Your session has been expired, please log in again."
            //   );
            //   this.$router.push("/login");
            // } else if (error.msg === "Not enough segments") {
            //   this.$message.warning(
            //     "Your session has been expired, please log in again."
            //   );
            //   this.$router.push("/login");
            // } else {
            //   this.Set_data_default_graph();
            //   // this.$router.push("/login");
            //   // this.$message.warning("please log in again.");
            // }
          });
      }
    },
    changeSpinning() {
      this.spinning = !this.spinning;
    },
    Open_DialogAdd_event_Meeting() {
      this.dialog = false;
      var data ={

        date_source_api : this.source_api,
        room_source_api: this.data_value
       }
      //  console.log(data);
      this.$refs.DialogAddMeeting.open(data);
    },
    handleCancel(e) {
      console.log(e);
      this.dialog = false;
    },
  },
};
</script>
<style scoped>
.spin-content {
  border: 1px solid #91d5ff;
  background-color: #e6f7ff;
  padding: 30px;
}
</style>
